.activity {

  &__item {
    min-height: 170px;
    display: flex;
    align-items: center;

    @include big-tablet {
      grid-column: 6 span;
    }

    @include small-tablet {
      min-height: 150px;
      grid-column: 12 span;
    }

    @include big-mobile {
      min-height: 100px;
    }

    &:nth-child(4) {
      grid-column: 1/7;

      @include big-tablet {
        grid-column: 6 span;
      }

      @include small-tablet {
        grid-column: 12 span;
    }
    }

    &:nth-child(5) {
      grid-column: 7/13;

      @include big-tablet {
        grid-column: 6 span;
      }

      @include small-tablet {
        grid-column: 12 span;
    }
    }

    &:nth-child(9) {
      grid-column: 3/7;

      @include big-tablet {
        grid-column: 6 span;
      }

      @include small-tablet {
        grid-column: 12 span;
    }
    }

    .teams__item-title {
      margin-bottom: 0;
    }
  }

  &__item--seo {
    background-image: url(../img/admin.svg);
  }

  &__item--digital {
    background-image: url(../img/admin.svg);
  }

  &__item--smm {
    background-image: url(../img/admin.svg);
  }

  &__item--context {
    background-image: url(../img/admin.svg);
  }

  &__item--target {
    background-image: url(../img/admin.svg);
  }

  &__item--content-market {
    background-image: url(../img/admin.svg);
  }

  &__item--craud-market {
    background-image: url(../img/admin.svg);
  }

  &__item--generation {
    background-image: url(../img/admin.svg);
  }

  &__item--email {
    background-image: url(../img/admin.svg);
  }

  &__item--analytics {
    background-image: url(../img/admin.svg);
  }
}
