/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin large-desktop {
  @media (max-width: (1770px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1640px)) {
    @content;
  }
}

@mixin big-tablet {
  @media (max-width: (1240px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin big-mobile {
  @media (max-width: (750px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
