.faq {
  padding-bottom: 120px;

  @include mobile {
    padding-bottom: 40px;
  }

  &__details {
    position: relative;
    display: block;
    margin: 0 auto 10px;
    transition: 0.3s;
    overflow: hidden;
    border-bottom: 1px solid var(--blue-color);

    &::after {
      position: absolute;
      right: 24px;
      top: 25px;
      content: "";
      display: block;
      width: 14px;
      height: 14px;
      border-right: 3px solid var(--haiti-color);
      border-bottom: 3px solid var(--haiti-color);
      transform: rotate(-45deg);
      transform-origin: center;
      transition: .3s;
    }

    &[open]::after {
      transform: rotate(45deg);
    }

    &[open] {
      padding-bottom: 100px;
    }

    &[open] .faq__details-title {
      margin-bottom: 32px;

      @include small-tablet {
        margin-bottom: 26px;
      }
    }

    &::-webkit-details-marker {
      display: none;
    }
  }

  &__details-title {
    color: var(--haiti-color);
    font-size: 24px;
    font-weight: 700;
    line-height: 44px;
    text-transform: uppercase;
    padding: 12px 24px;
    cursor: pointer;
    list-style: none;
    transition: .3s;

    @include small-tablet {
      font-size: 20px;
      line-height: 40px;
      padding: 12px 40px 12px 24px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 24px;
    }

    &::-webkit-details-marker {
      display: none;
    }
  }

  &__details-content {
    color: var(--haiti-color);
    font-size: 24px;
    font-weight: 400;
    line-height: 38px;
    padding: 0 32px;
    margin-bottom: -50px;

    @include small-tablet {
      font-size: 18px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 24px;
    }
  }
}
