.price {
  margin-top: 150px;
  padding-top: 240px;

  @include big-tablet {
    margin-bottom: 30px;
  }

  @include tablet {
    padding-top: 150px;
  }

  &::before {
    height: 67%;
  }

  &::after {
    height: 67%;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 36px;

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
    }
  }

  &__item {
    grid-column: 4 span;
    border-radius: 50px;
    background: #FDFCF9;
    box-shadow: 5px 5px 20px 0px rgba(22, 22, 22, 0.07);
    padding: 40px 30px;

    @include big-tablet {
      grid-column: 6 span;
    }
  }

  &__title {
    color: #000;
    font-size: 40px;
    font-weight: 700;
    line-height: 60px;
    margin: 0;
    border-bottom: 2px solid rgba(29, 57, 122, 0.15);
    padding-bottom: 20px;
    margin-bottom: 20px;

    @include big-tablet {
      font-size: 30px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 40px;
      padding-bottom: 10px;
      margin-bottom: 10px;
    }
  }

  &__descr {

    &:not(:last-child) {
      border-bottom: 2px solid rgba(29, 57, 122, 0.15);
      padding-bottom: 20px;
    }
  }

  &__name {
    color: #000;
    font-family: Acrom;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 44px;
    opacity: .2;
    margin-bottom: 2px;
    display: block;

    @include mobile {
      font-size: 16px;
      line-height: 28x;
    }
  }

  &__text {
    color: #000;
    font-family: Acrom;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 60px;
    display: block;

    @include big-tablet {
      font-size: 20px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
    }
  }

  &__marketing-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 90px;

    @include big-mobile {
      flex-direction: column;
      align-items: flex-start;
    }

    @include mobile {
      margin-top: 50px;
      margin-left: 20px;
    }
  }

  &__marketing-item {
    color: #000;
    font-size: 20px;
    font-weight: 500;
    list-style-type: disc;
    margin-bottom: 5px;

    @include mobile {
      font-size: 18px;
    }
  }
}
