.departments {
  // margin-left: 50px;
  // margin-right: 50px;
  // margin-top: 555px;
  position: relative;
  padding-top: 0;
  padding-bottom: 110px;

  @include big-desktop {
    // margin-left: 40px;
    // margin-right: 40px;
    // margin-top: 455px;
    padding-bottom: 165px;
  }

  @include tablet {
    // margin-left: 20px;
    // margin-right: 20px;
    // margin-top: 520px;
    padding-top: 0;
    padding-bottom: 80px;
  }

  @include mobile {
    margin-left: 0;
    margin-right: 0;
    padding-top: 310px;
    // padding-bottom: 200px;
  }

  &::before {
    content: "";
    position: absolute;
    background-color: var(--blue-color);
    transform: skewY(8.5deg) translate(-50%);
    border-radius: 50px;
    width: 100%;
    height: 80%;
    left: 50%;
    top: -10px;
    z-index: -1;
    max-width: 1920px;

    @include big-desktop {
      top: -140px;
    }

    @include tablet {
      top: -85px;
    }

    @include mobile {
      top: 140px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    background-color: var(--blue-color);
    transform: skewY(-8.5deg) translateX(-50%);
    border-radius: 50px;
    width: 100%;
    height: 80%;
    left: 50%;
    bottom: 0;
    z-index: -1;
    max-width: 1920px;
  }

  &__duplicate {
    overflow: hidden;
    max-width: 1920px;
    margin: 0 auto;

    @include tablet {
      margin-top: -40px;
    }
  }

  &__duplicate-text {
    font-weight: 700;
    font-size: 128px;
    line-height: 1;
    text-transform: uppercase;
    opacity: 0.04;
    color: var(--light-color);
    white-space: nowrap;
    margin: 0;

    @include big-desktop {
      font-size: 94px;
      line-height: 100px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 76px;
    }

    @include mobile {
      font-size: 35px;
      line-height: 48px;
    }
  }

  &__duplicate-text--b {
    display: flex;
    justify-content: flex-end;
    transform: translateX(30px);

    @include big-desktop {
    transform: translateX(5px);

    }
  }

  &__container {
    position: relative;

    @include big-desktop {
      padding-left: 60px;
      padding-right: 60px;
    }

    @include tablet {
      padding-left: 30px;
      padding-right: 30px;
    }

    @include mobile {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__marketing {
  /* position: absolute;
  bottom: -430px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%); */
  margin-left: auto;
  margin-right: auto;
  margin-top: -25px;
  margin-bottom: -430px;
  position: relative;
  padding: 64px 84px;

    @include big-desktop {
      padding: 45px 60px;
      margin-bottom: -340px;
    }

    @include tablet {
      padding: 40px 45px;
      margin-bottom: -220px;
    }

    @include mobile {
      padding: 20px;
      margin-bottom: -295px;
    }
  }
}
