.graph-modal {
  background: rgba(29, 57, 122, 0.2);
  backdrop-filter: blur(10px);
}

.graph-modal__container {
  max-width: 1440px;
  width: 90%;
  box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
  border-radius: 50px;
  padding: 64px 94px;

  @include tablet {
    padding: 30px 20px;
    border-radius: 30px;
  }
}

.graph-modal__close {
  background-image: url(../img/close.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 24px;
  height: 24px;
  right: 35px;
  top: 25px;
  z-index: 10;
}

.modal {

  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 58px;
    text-transform: uppercase;
    color: var(--blue-color);
    position: relative;
    margin: 0;
    margin-bottom: 40px;

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }
  }

  &__title--shadow {
    position: absolute;
    left: 35px;
    top: 15px;
    font-weight: 700;
    font-size: 64px;
    line-height: 77px;
    text-transform: uppercase;
    color: var(--blue-color);
    opacity: 0.1;

    @include tablet {
      font-size: 20px;
      line-height: 32px;
    }
  }

  &__text {
    font-weight: 700;
    font-size: 32px;
    line-height: 54px;
    color: var(--haiti-color);
    margin: 0;
    margin-bottom: 40px;

    @include tablet {
      font-size: 15px;
      line-height: 22px;
    }
  }

  &__text--center {
    text-align: center;
    margin-bottom: 0;
  }

  &__form {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 36px;

    @include tablet {
      gap: 20px;
    }
  }

  &__form-input {
    border: 2px solid var(--sand-color);
    border-radius: 50px;
    font-weight: 700;
    font-size: 24px;
    line-height: 44px;
    padding: 12px 54px;
    color: var(--sand-color);
    grid-column: 6 span;

    @include tablet {
      font-size: 18px;
      line-height: 28px;
      grid-column: 12 span;
      padding: 12px 34px;
    }

    &::placeholder {
      color: var(--sand-color);
    }
  }

  &__form-btn {
    border: 2px solid var(--blue-color);
    border-radius: 50px;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    text-transform: uppercase;
    color: var(--blue-color);
    padding: 20px;
    background-color: transparent;
    display: inline-block;
    grid-column: 6 span;

    @include big-desktop {
      grid-column: 7 / span 6;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
      grid-column: 12 span;
      padding: 12px;
    }
  }

  &__text-info {
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    text-align: right;
    color: #000000;
    display: block;
    margin-top: 30px;

    @include tablet {
      font-size: 12px;
      line-height: 18px;
    }
  }

  &__form-range {
    grid-column: 2 span;
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    color: var(--haiti-color);
    text-align: center;

    @include big-desktop {
      grid-column: 4 span;
      align-self: center;
    }

    @include tablet {
      font-size: 15px;
      line-height: 24px;
      grid-column: 12 span;
    }
  }

  &__form-dash {
    background: rgba(255, 255, 255, 0.7);
    width: 10px;
    height: 2px;
    border-radius: 50px;

    @include tablet {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__form-times {
    position: relative;
    grid-column: 2 span;

    @include big-desktop {
      grid-column: 4 span;
    }

    @include tablet {
      grid-column: 6 span;
    }

    &:first-of-type {
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        right: -23px;
        transform: translateY(-50%);
        width: 10px;
        height: 2px;
        background-color: var(--sand-color);

        @include tablet {
          display: none;
        }
      }
    }
  }

  &__form-time {
    display: block;
    width: 100%;
    border: 2px solid var(--sand-color);
    border-radius: 50px;
    font-weight: 700;
    font-size: 24px;
    line-height: 44px;
    padding: 12px 32px;
    background: none;
    appearance: none;
    font-family: inherit;
    color: var(--sand-color);
    text-align: center;

    @include tablet {
      font-size: 18px;
      line-height: 28px;
      padding: 12px 14px;
    }

    &::-webkit-calendar-picker-indicator {
      opacity: 0;
      display: none;
    }
  }
}
