.features {
  // margin-left: 50px;
  // margin-right: 50px;
  // margin-bottom: 190px;
  position: relative;
  padding-top: 125px;
  padding-bottom: 370px;

  @include big-desktop {
    // margin-left: 40px;
    // margin-right: 40px;
    padding-top: 60px;
    padding-bottom: 220px;
  }

  @include tablet {
    // margin-left: 20px;
    // margin-right: 20px;
    padding-bottom: 200px;
    padding-top: 120px;
  }

  @include mobile {
    margin-left: 0;
    margin-right: 0;
    padding-top: 35px;
    padding-bottom: 230px;
  }

  &::before {
    content: "";
    position: absolute;
    background-color: var(--sand-bg);
    transform: skewY(-8.5deg) translateX(-50%);
    border-radius: 50px;
    width: 100%;
    height: 80%;
    left: 50%;
    top: -145px;
    z-index: -1;
    max-width: 1920px;

    @include mobile {
      top: -185px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    background-color: var(--sand-bg);
    transform: skewY(8.5deg) translateX(-50%);
    border-radius: 50px;
    width: 100%;
    height: 80%;
    left: 50%;
    bottom: 0;
    z-index: -1;
    max-width: 1920px;
  }

  &__duplicate {
    overflow: hidden;
    margin-bottom: 10px;
    max-width: 1920px;
    margin: 0 auto 10px;

    @include big-desktop {
      & .features__duplicate-text--t {
        -webkit-transform: translateX(-28px);
        -ms-transform: translateX(-28px);
        transform: translateX(-28px);
      }
    }
  }

  &__duplicate-text {
    font-weight: 700;
    font-size: 128px;
    line-height: 1;
    text-transform: uppercase;
    opacity: 0.1;
    color: var(--blue-color);
    white-space: nowrap;
    margin: 0;

    @include big-desktop {
      font-size: 94px;
      line-height: 100px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 1;
    }

    @include mobile {
      font-size: 35px;
    }
  }

  &__duplicate-text--b {
    display: flex;
    justify-content: flex-end;
  }

  &__container {
    position: relative;
  }

  &__block {
    counter-reset: section;

    &:not(:last-child) {
      margin-bottom: 20px;

      @include tablet {
        margin-bottom: 45px;
      }
    }

    @include big-desktop {
      padding-left: 40px;
    }

    @include tablet {
      padding-left: 10px;
      padding-right: 10px;
    }

    @include mobile {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 43px;
    text-transform: uppercase;
    color: var(--blue-color);
    margin: 0;
    margin-bottom: 35px;

    @include big-desktop {
      font-size: 26px;
      line-height: 40px;
      margin-bottom: 15px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
    }
  }

  &__item {
    background: #FDFCF9;
    box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
    border-radius: 50px;
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;
    color: var(--haiti-color);
    width: 48%;
    padding: 36px 40px 36px 125px;
    margin-bottom: 30px;

    @include big-desktop {
      font-size: 20px;
      line-height: 34px;
      padding: 30px 36px 30px 111px;
      margin-bottom: 20px;
    }

    @include big-tablet {
      width: 100%;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
      width: 100%;
      min-height: 130px;
      display: flex;
      align-items: center;
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
      min-height: 90px;
      padding: 17px 20px 20px 60px;
      border-radius: 30px;
      margin-bottom: 10px;
    }

    &::before {
      counter-increment: section;
      content: counter(section);
      position: absolute;
      left: 40px;
      top: 50%;
      transform: translateY(-50%);
      font-weight: 700;
      font-size: 100px;
      color: var(--blue-color);
      opacity: .1;

      @include mobile {
        font-size: 54px;
        left: 15px;
      }
    }
  }

  &__marketing {
    /* position: absolute;
  top: calc(100% - 210px);
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%); */
    margin-left: auto;
    margin-right: auto;
    margin-top: -325px;
    margin-bottom: 10px;
    position: relative;
    padding: 64px 84px;

    @include big-desktop {
      padding: 45px 60px;
      margin-top: -220px;
      margin-bottom: 10px;
    }

    @include tablet {
      padding: 40px 45px;
    }

    @include mobile {
      padding: 20px;
      margin-bottom: -150px;
    }
  }
}

.features-masonry__freelance {

  @include big-desktop {
    margin-bottom: 20px;
  }

  & .features-masonry__item:nth-child(odd) {
    padding: 46px 40px 46px 125px;

    @include big-desktop {
      padding: 40px 40px 40px 111px;
    }

    @include tablet {
      padding: 30px 36px 30px 111px;
    }

    @include mobile {
      padding: 17px 20px 20px 60px;
    }
  }
}

.features-masonry__agent {

  & .features-masonry__item:nth-child(5) {
    padding-bottom: 59px;

    @include mobile {
      padding-bottom: 20px;
    }
  }

  & .features-masonry__item:nth-child(even) {
    padding: 42px 40px 42px 125px;

    @include big-desktop {
      padding: 35px 40px 35px 111px;
    }

    @include tablet {
      padding: 30px 36px 30px 111px;
    }

    @include mobile {
      padding: 17px 20px 20px 60px;
    }
  }
}
