.logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;

  @include big-desktop {
    & .image {
      width: 40px;
      height: 40px;
    }
  }

  @include mobile {
    & .image {
      width: 35px;
      height: 35px;
    }
  }

  &__descr {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 250px;
    margin-left: 10px;

    @include tablet {
      width: 210px;
    }

    @include mobile {
      width: 170px;
      margin-left: 5px;
    }
  }

  &__text {
    font-weight: 600;
    font-size: 25px;
    line-height: 34px;
    text-transform: uppercase;
    color: var(--light-color);

    @include tablet {
      font-size: 20px;
    line-height: 26px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__text-small {
    color: #6f85b7;
    font-size: 17px;
    letter-spacing: 0.5px;
    font-weight: 600;

    @include tablet {
      font-size: 14px;
      letter-spacing: 0.3px;
    }

    @include mobile {
      font-size: 11px;
    }
  }

  &__text--color {
    color: var(--blue-color);
  }
}
