.tabs {

  &__btns {
    margin-bottom: 50px;

    @include big-desktop {
      margin-bottom: 35px;
    }

    @include mobile {
      position: relative;
      margin-bottom: 50px;
    }

    &>div {
      @include mobile {
        -webkit-backdrop-filter: none !important;
        backdrop-filter: none !important;
      }
    }

    & div {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: -5px;

      @include tablet {
        margin-top: -15px;
        margin-bottom: 20px;
      }

      @include mobile {
        margin-top: 35px;
        top: 0 !important;
      }
    }
  }

  &__btns-text {
    font-weight: 700;
    font-size: 36px;
    line-height: 43px;
    text-transform: uppercase;
    color: var(--sand-color);

    @include big-desktop {
      font-size: 24px;
      line-height: 36px;
    }

    @include tablet {

      &:last-child {
        margin-left: 12px;
      }
    }

    @include mobile {
      font-size: 18px;
      line-height: 24px;
    }
  }

  &__btns-text--absolute {
    @include tablet {
      position: absolute;
      left: 0;
      top: -45px;
    }

    @include mobile {
      top: -25px;
    }
  }

  &__btns-text--absolute-bottom {
    @include mobile {
      position: absolute;
      bottom: -30px;
      left: -10px;
    }
  }

  &__click {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    margin-top: 10px;

    @include big-mobile {
      margin-top: 45px;
    }
  }

  &__click-text {
    margin-left: 7px;
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 auto;
    margin-left: 70px;
    margin-right: 70px;

    @include big-desktop {
      margin-left: 15px;
      margin-right: 15px;
      -ms-flex-pack: distribute;
      justify-content: space-around;
    }

    @include tablet {
      margin-left: 0;
      margin-right: 0;
    }

    @include big-mobile {
      -webkit-box-flex: 1;
      -ms-flex: auto;
      flex: auto;
      -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
      overflow-x: auto;
      -webkit-box-pack: justify;
      -ms-flex-pack: justify;
      justify-content: space-between;
    }
  }

  &__nav-item {
    @include big-mobile {
      flex: 0 0 auto;
      margin-bottom: 10px;
    }
  }

  &__nav-btn {
    font-weight: 700;
    font-size: 36px;
    line-height: 43px;
    color: var(--sand-color);
    border: 2px solid transparent;
    border-radius: 50px;
    padding: 12px 54px;
    transition: border .3s ease-in;

    @include big-desktop {
      font-size: 22px;
    line-height: 36px;
    padding: 12px 38px;
    }

    @include tablet {
      padding: 10px 32px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 24px;
      padding: 10px 20px;
    }

    &:focus,
    &:hover {
      border: 2px solid var(--sand-color);
      border-radius: 50px;
      padding: 12px 54px;
      background-color: transparent;
      color: var(--sand-color);

      @include big-desktop {
        padding: 12px 38px;
      }

      @include tablet {
        padding: 10px 32px;
      }

      @include mobile {
        padding: 10px 20px;
      }
    }
  }

  &__nav-btn--active {
    border: 2px solid var(--sand-color);
    border-radius: 50px;
    padding: 12px 54px;

    @include big-desktop {
      padding: 12px 38px;
    }

    @include tablet {
      padding: 10px 32px;
    }

    @include mobile {
      padding: 10px 20px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 54px;
    margin: 0;
    color: var(--light-color);
    margin-bottom: 20px;

    @include big-desktop {
      font-size: 26px;
      line-height: 40px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 10px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;
    margin: 0;
    color: var(--light-color);

    @include big-desktop {
      font-size: 20px;
      line-height: 34px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include tablet {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 30px;
    position: relative;

    @include big-tablet {
      flex-direction: column-reverse;
      margin-bottom: 40px;
    }

    @include mobile {
      margin-top: 20px;
      margin-bottom: 60px;
    }
  }

  &__img--transform {
    transform: translateX(100px);

    @include big-desktop {
      transform: translateX(0);
    }
  }

  &__list {
    background: #2F4C8F;
    box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
    border-radius: 50px;
    padding: 40px 30px;

    @include big-tablet {
      width: 100%;
      margin-bottom: 30px;
    }

    @include big-mobile {
      padding: 20px;
      border-radius: 30px;
      margin-bottom: 70px;
    }
  }

  &__item {
    &:not(:last-child) {
      border-bottom: 2px solid #1D397A;
      padding-bottom: 20px;
      margin-bottom: 20px;

      @include tablet {
        padding-bottom: 15px;
        margin-bottom: 10px;
      }

      @include mobile {
        padding-bottom: 10px;
      }
    }
  }

  &__item-name {
    font-weight: 500;
    font-size: 24px;
    line-height: 44px;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;

    @include big-desktop {
      font-size: 20px;
      line-height: 40px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__item-text {
    font-weight: 700;
    font-size: 36px;
    line-height: 60px;
    color: var(--light-color);
    margin: 0;

    @include big-desktop {
      font-size: 26px;
      line-height: 40px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 36px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
    }
  }

  &__subtitle {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 30px;

    @include tablet {
      font-size: 18px;
      line-height: 28px;
      margin-bottom: 10px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__img--dep1 {
    @include big-desktop {
      width: 550px;
      height: 398px;
    }

    @include big-mobile {
      width: 275px;
      height: 200px;
      transform: rotate(-90deg);
    }
  }

  &__img--dep2 {
    @include big-desktop {
      width: 678px;
      height: 360px;
    }

    @include big-mobile {
      width: 457px;
      height: 242px;
      transform: rotate(-90deg);
      max-width: inherit;
      margin-top: 60px;
      margin-bottom: 60px;
    }
  }

  &__img--dep3 {
    @include big-desktop {
      width: 676px;
      height: 323px;
    }

    @include big-mobile {
      width: 517px;
      height: 247px;
      transform: rotate(-90deg);
      max-width: inherit;
      margin-top: 80px;
      margin-bottom: 80px;
    }
  }
}
