.social {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  margin-left: 24px;

  @include small-tablet {
    margin-left: 20px;
  }

  &-inner {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }

  &__item-text {
    margin-right: 36px;

    @include tablet {
      display: none;
    }
  }

  &__item {

    &:not(:last-child) {
      margin-right: 36px;

      @include big-desktop {
        margin-right: 23px;
      }

      @include tablet {
        margin-right: 14px;
      }

      @include small-tablet {
        margin-right: 12px;
      }
    }
  }

  &__link {
    display: block;
    width: 36px;
    height: 36px;

    @include tablet {
      width: 24px;
      height: 24px;
    }



    & svg {
      -webkit-transition: fill 0.3s ease-in-out;
      -o-transition: fill 0.3s ease-in-out;
      transition: fill 0.3s ease-in-out;
      display: block;
      width: 100%;
      height: 100%;
      -o-object-fit: cover;
      object-fit: cover;
      -webkit-transition: fill 0.3s ease-in-out;
      -o-transition: fill 0.3s ease-in-out;
      transition: fill 0.3s ease-in-out;
      fill: var(--light-color);
    }

    &:hover svg {
      fill: var(--sand-color);
    }
  }

  &__link--tg {
    width: 41px;
  }
}
