.hero {
  padding-top: 230px;
  padding-bottom: 150px;
  // margin-left: 50px;
  // margin-right: 50px;
  margin-bottom: 100px;
  position: relative;

  @include big-desktop {
    // margin-left: 40px;
    // margin-right: 40px;
    padding-top: 160px;
    padding-bottom: 170px;
  }

  @include tablet {
    // margin-left: 20px;
    // margin-right: 20px;
    padding-top: 110px;
    padding-bottom: 150px;
  }

  @include mobile {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 130px;
    padding-top: 85px;
    padding-bottom: 160px;
  }

  &::before {
    content: "";
    position: absolute;
    background-color: var(--blue-color);
    -webkit-transform: skewY(-8.5deg) translateX(-50%);
    -ms-transform: skewY(-8.5deg) translateX(-50%);
    transform: skewY(-8.5deg) translateX(-50%);
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
    width: 100%;
    height: 120%;
    left: 50%;
    top: -33%;
    z-index: -1;
    max-width: 1920px;
  }

  &::after {
    content: "";
    position: absolute;
    background-image: url(../img/hero-pic.svg);
    background-repeat: no-repeat;
    background-position: center center;
    left: 0;
    top: 55px;
    width: 100%;
    height: 100%;
    z-index: -1;

    @include big-desktop {
      left: 70px;
      top: 20px;
      background-size: cover;
    }

    @include tablet {
      display: none;
    }
  }

  &__duplicate {
    overflow: hidden;
    max-width: 1920px;
    margin: 0 auto;

    @include mobile {
      margin-top: -20px;
    }
  }

  &__duplicate-text {
    font-weight: 700;
    font-size: 128px;
    line-height: 1;
    text-transform: uppercase;
    opacity: 0.04;
    color: var(--light-color);
    white-space: nowrap;
    margin: 0;

    @include big-desktop {
      font-size: 94px;
      line-height: 100px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 1;
    }

    @include mobile {
      font-size: 35px;
    }
  }

  &__duplicate-text--t {
    transform: translateX(20%);

    @include big-desktop {
      transform: translateX(0);
    }

    @include tablet {
      transform: translateX(-25px);
    }

    @include mobile {
      transform: translateX(-45px);
    }
  }

  &__duplicate-text--b {
    display: flex;
    justify-content: flex-end;
    transform: translateX(-11%);

    @include big-desktop {
      transform: translateX(-7%);
    }
  }

  &__container {
    position: relative;
    margin-bottom: 50px;

    @include big-desktop {
      padding-left: 60px;
      padding-right: 60px;
      margin-bottom: 20px;
    }

    @include tablet {
      padding-left: 30px;
      padding-right: 30px;
    }

    @include mobile {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__inn {
    position: relative;
  }

  &__title {
    font-weight: 700;
    font-size: 75px;
    line-height: 90px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 105px;
    position: relative;

    @include big-desktop {
      font-size: 58px;
      line-height: 72px;
      margin-bottom: 70px;
    }

    @include tablet {
      font-size: 40px;
      line-height: 54px;
      margin-bottom: 60px;
    }

    @include mobile {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 35px;
    }
  }

  &__title--shadow {
    position: absolute;
    text-shadow: 6px 6px 0px rgba(0, 0, 0, 0.2);
    left: 35px;
    top: 10px;
    font-weight: 700;
    font-size: 96px;
    line-height: 115px;
    text-transform: uppercase;
    opacity: 0.04;
    color: var(--light-color);

    @include big-desktop {
      font-size: 70px;
      line-height: 97px;
      left: 15px;
    }

    @include tablet {
      font-size: 40px;
      line-height: 66px;
    }

    @include mobile {
      font-size: 24px;
      line-height: 36px;
      left: 5px;
      white-space: nowrap;
    }
  }

  &__text-prev {
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;
    color: var(--light-color);
    margin: 0;
    max-width: 825px;
    margin-bottom: 45px;

    @include big-desktop {
      font-size: 20px;
      line-height: 34px;
      max-width: 765px;
      margin-bottom: 20px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 36px;
    line-height: 48px;
    margin: 0;
    color: var(--light-color);

    @include big-desktop {
      font-size: 26px;
      line-height: 48px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 36px;
    }
  }

  &__text-bold {
    font-weight: 700;
    font-size: 42px;
    line-height: 48px;

    @include big-desktop {
      font-size: 30px;
    }

    @include tablet {
      font-size: 28px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 36px;
    }
  }

  &__marketing {
    /* position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%);
  -ms-transform: translate(-50%);
  transform: translate(-50%); */
    padding: 64px 84px;
    margin-left: auto;
    margin-right: auto;
    margin-top: -300px;
    margin-bottom: -190px;
    position: relative;
    /* top: calc(100% - 150px); */

    @include big-desktop {
      padding: 45px 60px;
      margin-top: -280px;
      margin-bottom: -95px;
    }

    @include tablet {
      padding: 40px 45px;
      margin-top: -230px;
      margin-bottom: -100px;
    }

    @include mobile {
      padding: 25px;
      margin-top: -240px;
      margin-bottom: -10px;
    }
  }
}
