.procedure-service {

  &--bg {
    margin-top: -150px;
    padding-top: 240px;
    padding-bottom: 250px;

    @include big-tablet {
      margin-top: -110px;
      padding-top: 170px;
    }

    @include tablet {
      padding-bottom: 100px;
    }

    @include mobile {
      padding-top: 330px;
    }

    &::before {
      transform: skewY(-8.5deg) translate(-50%);
      height: 67%;
    }

    &::after {
      transform: skewY(8.5deg) translate(-50%);
      height: 67%;
    }
  }

  &--bgcolor {

    &::before {
      height: 92%;
    }

    &::after {
      height: 92%;
    }
  }

  &__title {
    color: #DED1B2;
    font-size: 32px;
    font-weight: 700;
    line-height: 54px;
    margin: 0;
    margin-bottom: 24px;

    @include mobile {
      font-size: 20px;
      line-height: 40px;
    }
  }

  &__text {
    color: #fff;
    font-size: 24px;
    font-weight: 400;
    line-height: 38px;
    margin: 0;
    margin-bottom: 12px;

    @include mobile {
      font-size: 16px;
      line-height: 25px;
    }
  }

  &__text--color {
    color: #fff;
  }

  &__descr {
    color: #fff;
    font-size: 24px;
    font-weight: 700;
    line-height: 38px;
    margin: 0;
    margin-top: 12px;
    margin-bottom: 24px;
  }

  &__item {
    background: rgba(255, 255, 255, 0.15);

    &::before {
      color: white;
    }
  }

  &__tabs {
    margin-top: 20px;
  }

  &--pt {
    @include mobile {
      padding-top: 220px;
    }
  }


}
