  &__btn {
    padding: 0;
    width: 100px;
    height: 130px;
    background: transparent;
    cursor: pointer;
  }

  &__txt {
    position: absolute;
    right: -555px;
    top: 50%;
    z-index: 3;
    width: 544px;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-50%);
    transition: opacity .3s ease-in-out, transform .3s ease-in-out, visibility .3s ease-in-out;
    border-radius: 30px;
    box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
    color: var(--haiti-color);
    background: var(--light-color);
    padding: 40px 30px;

    &::before {
      content: '';
      position: absolute;
      left: -5px;
      top: 50%;
      transform: translateY(-50%) rotate(45deg);
      width: 20px;
      height: 20px;
      background-color: var(--light-color);
    }

    @include tablet {
      padding: 30px;
      width: 290px;
      right: inherit;
      left: 50%;
      transform: translateY(0);
      transform: translateX(-50%);
    }
  }

  &__tit {
    font-weight: 700;
    font-size: 24px;
    line-height: 38px;
    display: block;
    margin-bottom: 20px;

    @include tablet {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;

    @include tablet {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__btn:hover+.tooltip__txt {
    opacity: 1;
    visibility: visible;
  }

  &__btn:focus+.tooltip__txt {
    opacity: 1;
    visibility: visible;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0)
}
