// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Acrom", sans-serif;
  --content-width: 1440px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --blue-color: #1d397a;
  --haiti-color: #0f0b24;
  --sand-color: #ded1b2;
  --sand-bg: #f9f5eb;
}
