.marketing {
  background: var(--light-color);
  box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
  border-radius: 50px;
  max-width: 1440px;
  width: 100%;
  z-index: 5;

  @include big-desktop {
    // max-width: 1170px;
  }

  @include tablet {
    width: 92%;
  }

  @include mobile {
    border-radius: 30px;
  }

  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 58px;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 45px;
    position: relative;

    @include big-desktop {
      font-size: 38px;
      line-height: 52px;
    }

    @include tablet {
      font-size: 28px;
      line-height: 40px;
      margin-bottom: 25px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
      margin-bottom: 15px;
    }
  }

  &__title--blue {
    color: var(--blue-color);
  }

  &__title--sand {
    color: var(--sand-color);
  }

  &__title-shadow {
    position: absolute;
    left: 35px;
    top: 15px;
    font-weight: 700;
    font-size: 64px;
    line-height: 77px;
    text-transform: uppercase;
    opacity: 0.1;

    @include big-desktop {
      font-size: 54px;
      line-height: 68px;
    }

    @include tablet {
      font-size: 36px;
      line-height: 48px;
      left: 5px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 32px;
      top: 7px;
    }

  }

  &__title-shadow--sand {
    opacity: .2;
  }

  &__subtitle {
    font-weight: 700;
    font-size: 32px;
    line-height: 54px;
    margin: 0;
    margin-bottom: 25px;
    color: var(--haiti-color);

    @include big-desktop {
      font-size: 26px;
      line-height: 40px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 32px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 22px;
      margin-bottom: 20px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;
    margin: 0;
    color: var(--haiti-color);

    @include big-desktop {
      font-size: 20px;
      line-height: 34px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    width: 100%;
    max-width: 55%;

    @include big-desktop {
      max-width: 47%;
    }

    @include tablet {
      max-width: 55%;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start;
    }

    @include big-mobile {
      max-width: 100%;
      margin-top: 270px;
    }
  }

  &__wrap-text {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    text-transform: uppercase;
    color: var(--blue-color);
    margin-right: 10px;

    @include tablet {
      margin-bottom: 20px;
      font-size: 20px;
    }

    @include mobile {
      font-size: 17px;
    }
  }

  &__wrap-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }

  &__wrap-item {
    &:not(:last-child) {
      margin-right: 24px;
    }
  }

  &__wrap-link {
    display: block;
    width: 76px;
    height: 76px;

    @include mobile {
      width: 56px;
      height: 56px;

      & svg {
        width: 56px;
        height: 56px;
     }
    }

    & path {
      -webkit-transition: fill 0.3s ease-in-out;
      -o-transition: fill 0.3s ease-in-out;
      transition: fill 0.3s ease-in-out;
    }

    & rect {
      -webkit-transition: stroke 0.3s ease-in-out;
      -o-transition: stroke 0.3s ease-in-out;
      transition: stroke 0.3s ease-in-out;
    }
  }
}

.marketing__wrap-link:hover path {
  fill: var(--sand-color);
}

.marketing__wrap-link:hover rect {
  stroke: var(--sand-color);
}
