.teams {
  position: relative;
  padding-top: 65px;
  margin-bottom: 75px;

  @include big-desktop {
    margin-bottom: 50px;
  }

  @include tablet {
    padding-top: 100px;
  }

  @include mobile {
    padding-top: 175px;
  }

  &__container {

    @include big-desktop {
      padding-left: 95px;
      padding-right: 95px;
    }

    @include big-tablet {
      padding-left: 50px;
      padding-right: 50px;
    }

    @include mobile {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 43px;
    text-transform: uppercase;
    color: var(--blue-color);
    margin: 0;
    margin-bottom: 40px;

    @include big-desktop {
      font-size: 26px;
      line-height: 40px;
    }

    @include big-tablet {
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 36px;

    @include big-tablet {
      gap: 20px;
    }

    @include mobile {
      gap: 10px;
    }
  }

  &__item {
    background: var(--light-color);
    box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
    border-radius: 50px;
    padding: 40px 30px;
    grid-column: 4 span;
    background-repeat: no-repeat;
    background-size: 90px 90px;
    background-position: 30px 45px;
    min-height: 460px;

    @include big-desktop {
      padding: 25px 20px;
      background-size: 80px 80px;
      background-position: 30px 35px;
    }

    @include big-tablet {
      grid-column: 12 span;
      min-height: 235px;
      background-size: 70px 70px;
      padding: 50px 30px 30px 30px;
    }

    @include mobile {
      padding: 20px;
      background-size: 40px 40px;
      background-position: 20px 25px;
      min-height: 218px;
      border-radius: 30px;
    }

    &:nth-child(4) {
      grid-column: 3/7;

      @include big-tablet {
        grid-column: 12 span;
      }
    }
  }

  &__item-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 44px;
    text-transform: uppercase;
    color: var(--haiti-color);
    margin: 0;
    margin-bottom: 34px;
    padding-left: 115px;

    @include big-desktop {
      font-size: 20px;
      line-height: 42px;
      margin-bottom: 20px;
    }

    @include big-tablet {
      font-size: 18px;
      line-height: 28px;
      padding-left: 90px;
      margin-bottom: 40px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
      margin-bottom: 10px;
      padding-left: 55px;

    }
  }

  &__item--managers {
    background-image: url(../img/managers.svg);
  }

  &__item--commerce {
    background-image: url(../img/commerce.svg);
  }

  &__item--content {
    background-image: url(../img/content.svg);

    @include big-desktop {
      background-position: 30px 30px;
    }

    @include mobile {
      background-position: 20px 25px;
    }
  }

  &__item--admin {
    background-image: url(../img/admin.svg);

    @include big-desktop {
      & .teams__item-title {
        padding-left: 100px;
      }
    }

    @include big-tablet {
      & .teams__item-title {
        padding-left: 90px;
      }
    }

    @include mobile {
      & .teams__item-title {
        padding-left: 55px;
      }
    }
  }

  &__item--mailing {
    background-image: url(../img/mailing.svg);
  }

  &__item-text {
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;
    color: var(--haiti-color);
    margin: 0;

    @include big-desktop {
      font-size: 20px;
      line-height: 34px;
    }

    @include big-tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__marketing {
    /* position: absolute;
  bottom: -575px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%); */
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;
    margin-bottom: -50px;
    position: relative;
    padding: 64px 84px;

    @include big-desktop {
      padding: 45px 60px;
    }

    @include big-tablet {
      padding: 40px 45px;
      margin-bottom: 10px;
    }

    @include mobile {
      padding: 20px;
      margin-bottom: -290px;
    }
  }
}
