.useful {
  padding-bottom: 30px;

  @include big-desktop {
    padding-bottom: 55px;
  }

  @include mobile {
    padding-bottom: 40px;
  }

  &__duplicate {
    overflow: hidden;
    margin-top: -60px;
    margin-bottom: 10px;
    max-width: 1920px;
    margin: -60px auto 10px;

    @include big-desktop {
      margin: -30px auto 0;
    }

    @include mobile {
      margin: -10px auto 10px;
    }
  }

  &__duplicate-text {
    font-weight: 700;
    font-size: 128px;
    line-height: 1;
    text-transform: uppercase;
    opacity: 0.1;
    color: var(--blue-color);
    white-space: nowrap;
    margin: 0;

    @include big-desktop {
      font-size: 94px;
      line-height: 100px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 76px;
    }

    @include mobile {
      font-size: 35px;
      line-height: 1;
    }
  }

  &__duplicate-text--t {
    transform: translateX(-20px);

    @include mobile {
      transform: translateX(-305px);
    }
  }

  &__duplicate-text--b {
    display: flex;
    justify-content: flex-end;
    transform: translateX(40px);

    @include mobile {
      transform: translateX(225px);
    }
  }

  &__container {
    position: relative;
    padding-bottom: 55px;

    @include big-desktop {
      padding-left: 100px;
      padding-right: 100px;
      padding-bottom: 0;

      @include tablet {
        padding-right: 10px;
        padding-left: 10px;
      }
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
    flex-wrap: nowrap;
    overflow-x: auto;

    @include mobile {
      margin-bottom: 25px;
    }
  }

  &__btn {
    margin-top: 30px;

    @include tablet {
      margin-left: 40px;
      margin-right: 40px;
    }

    @include mobile {
      margin-left: 10px;
      margin-right: 10px;
      margin-top: 10px;
    }

    & .show-more {
      background-color: var(--light-color);
      box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
      border-radius: 50px;
      text-align: center;
      padding: 16px;
      font-weight: 700;
      font-size: 24px;
      line-height: 54px;
      text-transform: lowercase;
      color: var(--haiti-color);
      width: 100%;

      @include tablet {
        font-size: 18px;
        line-height: 28px;
      }

      @include mobile {
        font-size: 15px;
        line-height: 24px;
        padding: 10px;
      }
    }
  }

  &__item {
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    text-transform: uppercase;
    color: var(--blue-color);
    cursor: pointer;
    flex: 0 0 auto;
    margin-bottom: 5px;

    &:not(:last-child) {
      margin-right: 30px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__item--active {
    border-bottom: 2px solid var(--blue-color);
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 20px;

    @include tablet {
      margin-left: 40px;
      margin-right: 40px;
    }

    @include mobile {
      margin-left: 10px;
      margin-right: 10px;
    }
  }

  &__wrapper-item {
    grid-column: 4 span;
    background: var(--light-color);
    box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
    border-radius: 50px;
    padding: 40px 30px;

    @include tablet {
      grid-column: 6 span;
      padding: 20px;
    }

    @include mobile {
      grid-column: 12 span;
      border-radius: 30px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 44px;
    text-transform: uppercase;
    color: var(--haiti-color);
    margin: 0;

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__img {
    margin-bottom: 15px;
    border-radius: 30px;

    @include big-desktop {
      height: 220px;
    }
  }
}

.is-visible {
  display: block !important;
}

.prom {
  grid-column: 12 span;
  width: 100%;
  display: flex !important;
  justify-content: space-between;
  align-items: flex-start;
  padding: 64px 84px;
  margin: 12px 0;

  @include big-desktop {
    padding: 35px 60px;
    flex-wrap: wrap;
  }

  @include tablet {
    padding: 40px;
  }

  @include mobile {
    padding: 20px;
  }

  &__form {
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;

    @include mobile {
      flex: 1 1 auto;
      width: 100%;
    }
  }

  &__form-email {
    border: 2px solid var(--sand-color);
    border-radius: 50px;
    font-weight: 700;
    font-size: 24px;
    line-height: 44px;
    padding: 12px 54px;
    color: var(--sand-color);
    margin-bottom: 48px;

    @include tablet {
      margin-bottom: 25px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
      margin-bottom: 12px;
    }
  }

  &__form-btn {
    border: 2px solid var(--blue-color);
    border-radius: 50px;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    text-transform: uppercase;
    color: var(--blue-color);
    padding: 20px;
    background-color: transparent;
    display: inline-block;

    @include mobile {
      font-size: 15px;
      line-height: 24px;
      padding: 12px;
    }
  }
}

.prom-block {

  &__descr {
    flex: 1 0 auto;

    @include mobile {
      flex: 1 1 auto;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 58px;
    text-transform: uppercase;
    color: var(--blue-color);
    position: relative;
    margin: 0;
    margin-bottom: 20px;

    @include big-desktop {
      font-size: 38px;
      line-height: 52px;
      margin-bottom: 35px;
    }

    @include tablet {
      font-size: 28px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
      margin-bottom: 15px;
    }
  }

  &__title--shadow {
    position: absolute;
    left: 35px;
    top: 20px;
    font-size: 54px;
    line-height: 60px;
    text-transform: uppercase;
    color: var(--blue-color);
    opacity: 0.1;
    margin: 0;

    @include big-desktop {
      font-size: 46px;
      line-height: 60px;
    }

    @include tablet {
      font-size: 36px;
      line-height: 48px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 32px;
      top: 5px;
      left: 5px;
    }
  }

  &__text {
    font-weight: 700;
    font-size: 32px;
    line-height: 54px;
    margin: 0;
    color: var(--haiti-color);

    @include big-desktop {
      font-size: 26px;
      line-height: 40px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 32px;
      margin-bottom: 30px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 22px;
      margin-bottom: 16px;
    }
  }
}

.useful__wrapper-item:nth-child(n + 14) {
  display: none;
}

.hide {
  opacity: 1;
  animation-name: a1;
  animation-fill-mode: forwards;
  display: none !important;
}

@keyframes a1 {
  to {
    opacity: 0;
    min-height: 0;
    width: 0;
    padding: 0;
    margin: 0;
    border: 0;
    grid-column: 0 span;
  }
}
