.tooltip__btn {
  padding: 0;
  width: 100px;
  height: 130px;
  background: transparent;
  cursor: pointer;
}
.tooltip__txt {
  position: absolute;
  right: -555px;
  top: 50%;
  z-index: 3;
  width: 544px;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-50%);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
  border-radius: 30px;
  box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
  color: var(--haiti-color);
  background: var(--light-color);
  padding: 40px 30px;
}
.tooltip__txt::before {
  content: "";
  position: absolute;
  left: -5px;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  width: 20px;
  height: 20px;
  background-color: var(--light-color);
}
@media (max-width: 1024px) {
  .tooltip__txt {
    padding: 30px;
    width: 290px;
    right: inherit;
    left: 50%;
    transform: translateY(0);
    transform: translateX(-50%);
  }
}
.tooltip__tit {
  font-weight: 700;
  font-size: 24px;
  line-height: 38px;
  display: block;
  margin-bottom: 20px;
}
@media (max-width: 1024px) {
  .tooltip__tit {
    font-size: 15px;
    line-height: 24px;
  }
}
.tooltip__text {
  font-weight: 400;
  font-size: 24px;
  line-height: 38px;
}
@media (max-width: 1024px) {
  .tooltip__text {
    font-size: 15px;
    line-height: 24px;
  }
}
.tooltip__btn:hover + .tooltip__txt {
  opacity: 1;
  visibility: visible;
}
.tooltip__btn:focus + .tooltip__txt {
  opacity: 1;
  visibility: visible;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.graph-modal {
  background: rgba(29, 57, 122, 0.2);
  backdrop-filter: blur(10px);
}

.graph-modal__container {
  max-width: 1440px;
  width: 90%;
  box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
  border-radius: 50px;
  padding: 64px 94px;
}
