  &::after {
    content: "";
    position: absolute;
    background-color: var(--blue-color);
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    border-radius: 50px;
    width: 100%;
    height: 95.5%;
    left: 50%;
    bottom: 0;
    z-index: -1;
    max-width: 1920px;
  }

  &__container {

    @include big-desktop {
      padding-left: 55px;
      padding-right: 55px;
    }

    @include tablet {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 14px;

    @include big-desktop {
      margin-bottom: 15px;
    }

    @include tablet {
      margin-bottom: 30px;
    }

    @include small-tablet {
      flex-wrap: wrap;
    }

    @include mobile {
      margin-bottom: 15px;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start;
    }

    & .logo {
      @include small-tablet {
        flex: -1 0 50%;
      }

      @include mobile {
        flex: 1 0 auto;
        margin-bottom: 14px;
      }
    }
  }

  &__top-block {
    @include small-tablet {
      flex: 1 0 50%;
    }
  }

  &__top-inn {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    @include small-tablet {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
    }

    & .footer__top-text:first-child {
      margin-right: 20px;

      @include small-tablet {
        margin-right: 20px;
      }
    }
  }

  &__top-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 38px;
    text-align: right;
    color: var(--light-color);
    margin: 0;

    @include tablet {
      font-size: 16px;
      line-height: 34px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 28px;
      text-align: left;
    }
  }

  &__btn {
    display: inline-block;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: var(--light-color);
    text-transform: uppercase;
    border: 2px solid var(--light-color);
    border-radius: 50px;
    padding: 20px 80px;
    -webkit-transition: color 0.3s ease-in-out, border 0.3s ease-in-out;
    -o-transition: color 0.3s ease-in-out, border 0.3s ease-in-out;
    transition: color 0.3s ease-in-out, border 0.3s ease-in-out;

    &:hover {
      border: 2px solid var(--sand-color);
      color: var(--sand-color);
    }

    @include tablet {
      font-size: 15px;
      line-height: 24px;
      padding: 15px 35px;
    }

    @include small-tablet {
      flex: 0 0 100%;
      order: -1;
      width: 100%;
      text-align: center;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
      padding: 10px;
      width: 100%;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__bottom-text {
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: var(--light-color);
    margin: 0;

    @include mobile {
      text-align: left;
      width: 100%;
    }

    &:not(:last-child) {
      margin-right: 60px;

      @include tablet {
        margin-right: 0;
      }

      @include mobile {
        margin-bottom: 7px;
      }
    }
  }

  &__bottom-block {
    display: flex;
    align-items: center;

    @include tablet {
      flex-direction: column;
    }

    @include mobile {
      text-align: left;
      width: 100%;
    }
  }

  &__bottom-block-wrapper {
    display: flex;
    margin-right: 65px;

    @include tablet {
      align-items: center;
      margin-right: 0;
      margin-top: 10px;
      margin-bottom: 5px;

      & .footer__bottom-text:first-child {
        margin-right: 40px;

        @include mobile {
          margin-right: 0;
          margin-bottom: 10px;
        }
      }
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__soc {
    padding-bottom: 30px;
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: 2px solid rgba(255, 255, 255, 0.15);

    @include small-tablet {
      flex-direction: column-reverse;
    }
  }

  &__soc-desc {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    @include small-tablet {
      margin-bottom: 30px;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__soc-text {
    color: #FFF;
    font-size: 24px;
    font-weight: 700;
    line-height: 28px;
    text-transform: uppercase;
    margin-right: 36px;

    @include small-tablet {
      font-size: 18px;
    }

    @include mobile {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  &__soc-list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
}
