.result {
  &__list {}

  &__item {
    padding: 40px 30px;
    background: rgba(255, 255, 255, 0.15);
    box-shadow: 5px 5px 20px 0px rgba(22, 22, 22, 0.07);

    &::before {
      display: none;
    }

    @include tablet {
      display: block;
    }
  }

  &__item-title {
    color: #FFF;
    font-size: 40px;
    font-weight: 700;
    line-height: 60px;
    margin: 0;
    margin-bottom: 20px;

    @include big-tablet {
      font-size: 30px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 40px;
      padding-bottom: 10px;
      margin-bottom: 10px;
    }
  }

  &__item-subtitle {
    color: #FFF;
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 40px;

    @include big-tablet {
      font-size: 20px;
      line-height: 40px;
    }

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__item-descr {
    &:not(:last-child) {
      margin-bottom: 40px;

      @include mobile {
        margin-bottom: 10px;
      }
    }
  }

  &__item-name {
    color: #FFF;
    font-size: 24px;
    font-weight: 500;
    line-height: 44px;
    opacity: 0.2;

    @include mobile {
      font-size: 16px;
      line-height: 28x;
    }
  }

  &__item-text {
    color: #FFF;
    font-size: 36px;
    font-weight: 700;
    line-height: 60px;

    @include big-tablet {
      font-size: 30px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
    }
  }
}

.result__item-wrap {
  justify-content: start;
  margin-bottom: 100px;

  @include big-tablet {
    margin-bottom: 10px;
  }
}
