.departments--bgcolor {
  max-width: 1920px;
  width: 100%;
  background-color: var(--blue-color);
  margin: 0 auto;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;

  &:before {
    border-bottom-left-radius: 0;
    height: 81%;

    @include tablet {
      height: 100%;
    }
  }

  &:after {
    border-top-left-radius: 0;
  }
}

.process--bg {
  padding-top: 350px;

  @include tablet {
    padding-top: 190px;  }
}
