.offer {
  // margin-left: 50px;
  // margin-right: 50px;
  position: relative;
  padding-top: 165px;
  padding-bottom: 70px;

  @include big-desktop {
    // margin-left: 40px;
    // margin-right: 40px;
    margin-top: -60px;
    padding-top: 125px;
    padding-bottom: 55px;
  }

  @include tablet {
    // margin-left: 20px;
    // margin-right: 20px;
    padding-bottom: 45px;
  }

  @include mobile {
    margin-left: 0;
    margin-right: 0;
    margin-top: 0;
    padding-top: 270px;
    padding-bottom: 45px;
  }

  &::before {
    content: "";
    position: absolute;
    background-color: var(--blue-color);
    transform: skewY(8.5deg) translate(-50%);
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    width: 100%;
    height: 85%;
    left: 50%;
    top: -18px;
    z-index: -1;
    max-width: 1920px;
  }

  &::after {
    content: "";
    position: absolute;
    background-color: var(--blue-color);
    transform: translateX(-50%);
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
    width: 100%;
    height: 90%;
    left: 50%;
    bottom: 0;
    z-index: -1;
    max-width: 1920px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: start;

    @include big-desktop {
      padding-left: 40px;
      padding-right: 40px;
    }

    @include tablet {
      padding-left: 10px;
      padding-right: 10px;
    }

    @include mobile {
      flex-direction: column;
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__block {
    flex: 1 0 auto;
    position: relative;

    @include tablet {
      margin-left: 30px;
      display: flex;
      flex-direction: column;
      min-height: 100%;
      flex-shrink: 1;
    }

    @include mobile {
      margin-left: 0;
      width: 100%;
    }

    &:last-child {
      margin-left: 50px;

      @include mobile {
        margin-left: 0;
        margin-top: 40px;
      }
    }
  }

  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 40px;
    color: var(--light-color);
    text-transform: uppercase;
    margin: 0;

    @include big-desktop {
      font-size: 20px;
      line-height: 38px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 40px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 25px;

    @include big-desktop {
      font-size: 20px;
      line-height: 38px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__btn {
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    color: var(--light-color);
    text-transform: uppercase;
    border: 2px solid var(--light-color);
    border-radius: 50px;
    padding: 16px;
    width: 100%;

    @include tablet {
      font-size: 18px;
      line-height: 26px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
      padding: 10px;
    }
  }
}
