.process {
  /* margin-left: 50px; */
  /* margin-right: 50px; */
  /* margin-top: 130px; */
  margin-top: 110px;
  position: relative;
  padding-top: 240px;
  padding-bottom: 330px;

  @include big-desktop {
    padding-top: 210px;
    /* margin-left: 40px; */
    /* margin-right: 40px; */
    margin-top: 90px;
  }

  @include tablet {
    // margin-left: 20px;
    // margin-right: 20px;
    padding-top: 95px;
  }

  @include mobile {
    margin-left: 0;
    margin-right: 0;
    padding-top: 190px;
    padding-bottom: 220px;
  }

  &::before {
    content: "";
    position: absolute;
    background-color: var(--sand-bg);
    transform: skewY(-8.5deg) translateX(-50%);
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    width: 100%;
    height: 60%;
    left: 50%;
    top: -10px;
    z-index: -1;
    max-width: 1920px;
  }

  &::after {
    content: "";
    position: absolute;
    background-color: var(--sand-bg);
    transform: skewY(8.5deg) translateX(-50%);
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
    width: 100%;
    height: 60%;
    left: 50%;
    bottom: 0;
    z-index: -1;
    max-width: 1920px;
  }

  &__duplicate {
    overflow: hidden;
    max-width: 1920px;
    margin: 0 auto;

    @include mobile {
      margin-bottom: 15px;
    }
  }

  &__duplicate-text {
    font-weight: 700;
    font-size: 128px;
    line-height: 1;
    text-transform: uppercase;
    opacity: 0.1;
    color: var(--blue-color);
    white-space: nowrap;
    margin: 0;

    @include big-desktop {
      font-size: 94px;
      line-height: 100px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 76px;
    }

    @include mobile {
      font-size: 35px;
      line-height: 1;
    }
  }

  &__duplicate-text--t {

    @include big-desktop {
      @include big-desktop {
        transform: translateX(-20px);
      }
    }

    @include mobile {
      transform: translateX(-17px);
    }
  }

  &__duplicate-text--b {
    display: flex;
    justify-content: flex-end;
    transform: translateX(50px);

    @include big-desktop {
      transform: translateX(20px);
    }

    @include mobile {
      transform: translateX(17px);
      justify-content: center;
    }
  }

  &__container {
    position: relative;

    @include big-desktop {
      padding-left: 60px;
      padding-right: 60px;
    }

    @include tablet {
      padding-left: 30px;
      padding-right: 30px;
    }

    @include mobile {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__list {
    counter-reset: process;
  }

  &__item {
    background: #FDFCF9;
    box-shadow: 5px 5px 20px rgba(22, 22, 22, 0.07);
    border-radius: 50px;
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;
    color: var(--haiti-color);
    padding: 36px 20px 36px 115px;
    margin-bottom: 30px;
    position: relative;
    margin-bottom: 35px;

    @include big-desktop {
      padding: 36px 20px 36px 100px;
      margin-bottom: 25px;
    }

    @include tablet {
      margin-bottom: 20px;
      padding: 32px 20px 36px 110px;
    }

    @include mobile {
      padding: 20px 20px 20px 60px;
      border-radius: 30px;
      margin-bottom: 10px;
    }

    &::before {
      counter-increment: process;
      content: counter(process);
      position: absolute;
      left: 30px;
      top: 50%;
      transform: translateY(-50%);
      font-weight: 700;
      font-size: 100px;
      color: var(--blue-color);
      opacity: .1;

      @include mobile {
        font-size: 54px;
        left: 15px;
      }
    }
  }

  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 44px;
    text-transform: uppercase;
    color: var(--blue-color);
    margin: 0;
    margin-bottom: 10px;

    @include big-desktop {
      font-size: 20px;
      line-height: 40px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;
    color: var(--haiti-color);
    margin: 0;

    @include big-desktop {
      font-size: 20px;
      line-height: 34px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 24px;
    }
  }

  &__marketing {
    /* position: absolute;
  bottom: -290px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%); */
    padding: 50px 60px;
    margin-left: auto;
    margin-right: auto;
    margin-top: -320px;
    margin-bottom: -140px;
    position: relative;

    @include big-desktop {
      padding: 55px 60px;
      margin-bottom: -95px;
    }

    @include tablet {
      padding: 40px 45px;
      overflow: hidden;
    }

    @include mobile {
      padding: 20px 20px 30px 20px;
      overflow: visible;
      margin-top: -200px;
      margin-bottom: -300px;
    }

    &::before {
      content: "";
      position: absolute;
      right: 85px;
      bottom: 0;
      background-image: url(../img/photo.png);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: right bottom;
      width: 400px;
      height: 100%;
      z-index: -1;

      @include big-desktop {
        right: 145px;
      }

      @include big-tablet {
        right: 30px;
      }

      @include tablet {
        right: -5px;
        width: 350px;
      }

      @include big-mobile {
        right: 0;
        left: 50%;
        bottom: 175px;
        transform: translateX(-50%);
        background-position: center 10px;
        width: 210px;
        height: 210px;
        background-color: #D9D9D9;
        border-radius: 50%;
      }
    }

    & .marketing__title {
      margin-bottom: 85px;

      @include big-desktop {
        margin-bottom: 55px;
      }

      @include mobile {
        margin-bottom: 15px;
      }
    }

    & .marketing__text {
      max-width: 55%;
      width: 100%;

      @include big-desktop {
        max-width: 47%;
      }

      @include tablet {
        max-width: 55%;
      }

      @include big-mobile {
        max-width: 100%;
      }

      &:not(:last-child) {
        margin-bottom: 40px;

        @include mobile {
          margin-bottom: 25px;
        }
      }
    }
  }
}
