.header {
  position: absolute;
  top: 10px;
  left: 0;
  width: 100%;
  z-index: 500;

  &__container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    border-bottom: 2px solid rgba(249, 245, 235, 0.1);
    padding-top: 10px;
    padding-bottom: 20px;

    @include big-desktop {
      padding-left: 60px;
      padding-right: 60px;
      padding-bottom: 10px;
    }

    @include tablet {
      padding-left: 35px;
      padding-right: 35px;
      padding-top: 0;
    }

    @include mobile {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    gap: 30px;

    @include big-desktop {
      gap: 14px;
    }

    @include big-tablet {
      gap: 30px;
      max-width: 600px;
    }

    @include big-mobile {
      gap: 0;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: rgba(255, 255, 255, 0.4);
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-transition: color 0.3s ease-in-out;
    -o-transition: color 0.3s ease-in-out;
    transition: color 0.3s ease-in-out;

    @include big-desktop {
      font-size: 16px;
    }

    @include tablet {
      font-size: 15px;
      line-height: 22px;
      flex-basis: 12px;
      max-width: 170px;
    }

    @include big-mobile {
      font-size: 0;
      line-height: 0;
    }
  }

  &__text--h {
    text-align: right;

    &:hover {
      color: var(--sand-color);
    }

    &:hover .header__text-svg {
      fill: var(--sand-color);
    }
  }

  &__text-svg {
    width: 36px;
    height: 36px;
    -o-object-fit: cover;
    object-fit: cover;
    -webkit-transition: opacity 0.3s ease-in-out;
    -o-transition: opacity 0.3s ease-in-out;
    transition: opacity 0.3s ease-in-out;
    fill: var(--light-color);
    margin-left: 24px;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    -webkit-transition: fill 0.3s ease-in-out;
    -o-transition: fill 0.3s ease-in-out;
    transition: fill 0.3s ease-in-out;

    @include tablet {
      width: 24px;
      height: 24px;
      flex: 1 0 auto;
    }
  }

  &__text-none {
    margin-right: 5px;

    @include big-tablet {
      display: none;
    }
  }
}

.header--fixed {
  position: fixed;
  left: 0;
  top: 0;
  -webkit-animation: 1s forwards fixedMenu;
  animation: 1s forwards fixedMenu;
  background: rgba(255, 255, 255, 0.6);
  -webkit-box-shadow: 0px 4px 20px rgba(22, 22, 22, 0.07);
  box-shadow: 0px 4px 20px rgba(22, 22, 22, 0.07);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);

  & .logo__text {
    color: var(--blue-color);
  }

  & .header__text {
    color: rgba(111, 133, 183, .8);
  }

  &:hover .header__text--h {
    color: var(--sand-color);
  }

  & .social__link svg {
    fill: rgb(111, 133, 183);
  }

  & .header__text-svg {
    fill: #6F85B7;
    opacity: 1;
  }

  & .logo .image {
    display: none;
  }

  & .logo::before {
    content: "";
    background-image: url(../img/logo-fixed.svg);
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    background-size: contain;

    @include tablet {
      width: 40px;
      height: 40px;
    }

    @include mobile {
      width: 35px;
      height: 35px;
    }
  }

  & .header__container {
    padding-bottom: 0;
    border-bottom: none;
  }
}

@-webkit-keyframes fixedMenu {
  0% {
    padding: 0
  }

  50% {
    padding: 8px 0
  }

  100% {
    padding: 8px 0
  }
}
